@import "styles/variables";
@import "styles/mixins";

@keyframes ring {
  0% {
    transform: rotate(-30deg) translateX(40rem) translateY(40rem);
  }

  50% {
    transform: rotate(-30deg) translateX(-7rem) translateY(-12rem);
  }

  65% {
    transform: rotate(-28deg) translateX(-5rem) translateY(-10rem);
  }

  70% {
    transform: rotate(-32deg) translateX(-5rem) translateY(-10rem);
  }

  75% {
    transform: rotate(-28deg) translateX(-5rem) translateY(-10rem);
  }

  80% {
    transform: rotate(-32deg) translateX(-5rem) translateY(-10rem);
  }

  85% {
    transform: rotate(-28deg) translateX(-5rem) translateY(-10rem);
  }

  90% {
    transform: rotate(-32deg) translateX(-5rem) translateY(-10rem);
  }

  95% {
    transform: rotate(-28deg) translateX(-5rem) translateY(-10rem);
  }

  100% {
    transform: rotate(-30deg) translateX(-5rem) translateY(-10rem);
  }
}

.section {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: -50vw;
    width: 200vw;
    height: calc(100% - 10rem);
    background-color: $colorHighlightDark;
    z-index: 1;
  }

  .wrapper {
    position: relative;
    display: grid;
    grid-template-columns: 61.5rem;
    padding-bottom: 10rem;
    background-color: $colorHighlightDark;
    transition-duration: $transitionDuration;
    z-index: 2;
 }

  .imageWrapper {
    position: absolute;
    top: 15rem;
    right: 0;
    width: 60rem;
    height: 50rem;
    clip-path: polygon(0% 0%, 100% 0, 100% 19.5%, 68% 100%, 0% 100%);

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      right: 0;
      width: 40rem;
      height: 40rem;
      background-image: url("../../../assets/icons/ic_hugs.svg");
      background-size: cover;
      z-index: 0;
    }

    &::after {
      content: "";
      position: absolute;
      top: 10rem;
      right: 0;
      width: 31.7rem;
      height: 52.6rem;
      background-image: url("../../../assets/images/phone.png");
      background-size: cover;
      transition-duration: 1s;
      transform: rotate(-30deg) translateX(40rem) translateY(40rem);
    }
  }

  .imageWrapperActive {
    &::after {
      animation-name: ring;
      animation-fill-mode: forwards;
      animation-delay: 1s;
      animation-duration: 1s;
      animation-timing-function: ease-out;
    }
  }

  .title {
    @include h0;

    margin: 0;
    padding-right: 10%;
  }

  .descriptor {
    @include fontAccent;

    margin: 2rem 0 0 0;
  }

  .slogan {
    @include fontAccent($colorLightest, true);

    margin: 1rem 0 0 0;
  }

  .button {
    margin-top: 3.3rem;
    width: 24.7rem;
    color: $colorHighlightDark;
    background-color: $colorLightest;

    &:hover,
    &:active {
      box-shadow: 0 0 1rem 0 $colorLightest1;
    }
  }

  .hint {
    @include fontSmall;

    margin: 3.3rem 0 0 0;
    opacity: 0.4;
  }

  .line {
    position: relative;
    background-color: $colorHighlightLight;
    transform: rotate(-3deg);
    z-index: 2;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: -25vw;
      width: 50vw;
      height: 100%;
      background-color: $colorHighlightLight;
      z-index: -1;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: -25vw;
      width: 50vw;
      height: 100%;
      background-color: $colorHighlightLight;
      z-index: -1;
    }
  }

  .list {
    @include resetList;

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 6rem 0;
  }

  .item {
    @include fontLabels;

    opacity: 0.3;
  }
}

@media (max-width: $adaptiveMax) {
  .section {
    .wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-bottom: 38rem;

      &::before {
        content: "";
        position: absolute;
        bottom: -2rem;
        right: calc(50% - 30rem / 2);
        width: 30rem;
        height: 32rem;
        background-image: url("../../../assets/images/phone.png");
        background-size: cover;
      }

        &::after {
        display: none;
      }
    }

    .imageWrapper {
      display: none;
    }

    .title {
      padding-right: 0;
      text-align: center;
    }

    .descriptor {
      margin-top: 2.4rem;
      text-align: center;
    }

    .slogan {
      margin: 1.1rem 0 0 0;
      text-align: center;
    }

    .button {
      margin-top: 2.4rem;
      width: 100%;
    }

    .hint {
      margin: 1.3rem 0 0 0;
    }

    .list {
      padding: 4rem 1.5rem;
    }
  }
}
