@import "styles/variables";

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10rem;
  padding-bottom: 10rem;
  height: 100%;

  .wrapper {
    position: relative;
    display: flex;
    max-height: 100%;
  }

  .video {
    display: block;
    max-width: 100%;
    max-height: 100%;
  }

  .button {
    top: -5rem;
    right: calc(50% - 60rem);
  }
}

@media (max-width: $adaptiveMax) {
  .container {
    padding-top: 3rem;
    padding-bottom: 3rem;

    .button {
      top: -3rem;
      right: 0;
    }
  }
}
