@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  margin: 25vh auto 3rem auto;
  padding: 3rem;
  width: 70rem;
  background-color: $colorLightest;
}

.disclaimer {
  @include fontSmall($colorDarkest);

  margin: 2rem 0 0 0;
  opacity: 0.5;
}

.list {
  @include resetList;

  margin-top: 2rem;
}

.item {
  &:not(:first-child) {
    margin-top: 1rem;
  }
}

.link {
  @include fontSmall($colorHighlightDark);

  transition-duration: $transitionDuration;

  &:hover {
    color: $colorHighlightLight;
    text-decoration: underline;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    margin: 3rem auto;
    max-width: 90%;
    width: 100%;
  }
}
