@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}

.copyright {
  @include fontSmall($colorBgBtnPale);

  margin: 3.3rem 0 0 0;
}

.button {
  @include resetButton;

  position: relative;
  margin-left: 2rem;
  width: 3rem;
  height: 3rem;
  font-size: 0;
  background-color: $colorCopyright;
  border-radius: 50%;
  opacity: 0.8;
  transition-duration: $transitionDuration;

  &::before {
    content: "i";
    font-family: "Times New Roman", Serif;
    font-size: 2.4rem;
    color: $colorLightest;
  }

  &:hover,
  &:active {
    background-color: $colorHighlightDark;
  }
}
