@import "styles/variables";
@import "styles/mixins";

.groups {
  @include resetList;

  display: grid;
  grid-template-columns: 100%;
  grid-gap: 3.2rem;
}

.key {
  @include fontButtonStrong;

  margin: 0;
}

.value {
  @include fontSmall;

  margin: 0.5rem 0 0 0;
  padding-right: 10%;
  opacity: 0.6;
}

.list {
  @include resetList;

  margin-top: 1.6rem;
}

.item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 1.9rem;
  padding-bottom: 2.1rem;

  &:nth-of-type(2n + 1) {
    background-color: rgba(255, 255, 255, 0.15);
  }

  &:nth-of-type(2n) {
    background-color: rgba(255, 255, 255, 0.2);
  }
}

.label {
  margin: 0;
  font-family: "FuturaPT", Arial, sans-serif;
  font-size: 0.9rem;
  font-weight: 900;
  line-height: 1.2rem;
  color: $colorLightest;
  letter-spacing: 0.01rem;
}

.iconOK {
  width: 1.8rem;
  stroke: $colorHighlightLight;
  fill: none;
}

.resultsLabel {
  @include fontSmall;

  margin: 5rem 0 0 0;
  text-transform: uppercase;
  text-align: center;
  opacity: 0.59;
}

.results {
  @include resetList;

  margin-top: 2.3rem;
}

.link {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2.3rem 0.4rem;
  height: 100%;
}

.price {
  @include fontTrigger($colorLightest);

  margin-top: 3.4rem;
  font-size: 1.3rem;
  line-height: 2rem;
}

.notification {
  margin-top: 2.7rem;
  font-family: "FuturaPT", Arial, sans-serif;
  font-size: 0.8rem;
  font-weight: 600;
  line-height: 1.2rem;
  color: $colorLightest;
  text-align: center;
  opacity: 0.42;
}
