@import "styles/variables";
@import "styles/mixins";

.footer {
  background-color: $colorLightest;
}

.wrapper {
  padding-top: 5.3rem;
  padding-bottom: 2rem;
}

.copyright {
  @include fontCopyright;

  display: flex;
  margin: 0;
  text-align: center;

  a {
    @include fontCopyright;

    margin-left: 0.5rem;
  }
}

@media (max-width: $adaptiveMax) {
  .copyright {
    display: block;
  }
}
