@import "styles/variables";
@import "styles/mixins";

.wrapper {
  --padding-modal-left: 13.5rem;
  --padding-modal-right: 10.3rem;
  position: relative;
  margin: 0 auto 0 auto;
  padding-right: var(--padding-modal-right);
  width: 144rem;
  min-height: 100vh;
  background-color: var(--main-bg-color);
  transition-duration: $transitionDuration;
}

.wrapperBlack {
  --main-bg-color: rgb(34, 34, 34);
  --table-font-heading-size: 2.5rem;
  --table-font-heading-height: 3.3rem;
  --table-cell-odd-color: red;
}

.wrapperPurple {
  --main-bg-color: rgba(34,58,94,1);
  --table-font-heading-size: 2rem;
  --table-font-heading-height: 2.6rem;
}

.buttonClose {
  @include resetButton;

  position: absolute;
  top: 5.2rem;
  left: 6.8rem;

  .iconClose {
    width: 3.7rem;
    fill: $colorHighlightLight;
  }
}

.intro {
  padding-top: 3.8rem;
  padding-bottom: 1.3rem;
  padding-left: var(--padding-modal-left);
  width: 68.5rem;
}

.heading {
  @include fontCopyright($colorLightest, 300);

  margin: 0;
  line-height: 3rem;
  text-transform: uppercase;
  letter-spacing: 0.4rem;
  opacity: 0.59;
}

.list {
  @include resetList;

  display: flex;
  align-items: center;
  margin-top: 0.8rem;
}

.item {
  &:not(:first-child) {
    margin-left: 3rem;
  }
}

.trigger {
  @include resetButton;
  @include fontInputLabel($colorLightest, 600);

  text-transform: uppercase;
  border-bottom: 1px solid transparent;
  opacity: 0.3;
  transition-duration: $transitionDuration;
}

.triggerActive {
  color: $colorHighlightLight;
  border-bottom-color: $borderButtonTransparent;
  opacity: 1;
}

.table {
  margin-top: -10.8rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    margin: 0;
    padding-right: 0;
    width: 100%;
  }

  .header {
    padding: 2.8rem 1.5rem;

  }

  .logo {
    fill: $colorLightest;
    width: 7rem;
  }

  .buttonClose {
    top: 1.8rem;
    right: 0.5rem;
    left: auto;
    padding: 1rem;

    .iconClose {
      width: 1.5rem;
      fill: $colorLightest;
      stroke: $colorLightest;
      opacity: 0.5;
    }
  }

  .intro {
    margin-top: 3.5rem;
    padding: 0 1.5rem;
    width: 100%;
  }

  .heading {
    text-align: center;
    letter-spacing: 0.04rem;
  }

  .list {
    margin-top: 4rem;
  }

  .item {
    &:not(:first-child) {
      margin-left: 1rem;
    }
  }

  .trigger {
    padding-bottom: 0.6rem;
    font-size: 1.4rem;
    line-height: 1.8rem;
    text-align: left;
  }

  .table {
    margin-top: 3.5rem;
  }
}
