@import "styles/variables";
@import "styles/mixins";

.button {
  @include resetButton;

  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem;
  transition-duration: $transitionDuration;

  &:hover {
    background-color: $colorHighlightLight;

    .icon {
      fill: $colorLightest;
    }
  }

  .icon {
    width: 2rem;
    fill: $colorHighlightLight;
    transition-duration: $transitionDuration;
  }
}

@media (max-width: $adaptiveMax) {
  .button {
    .icon {
      width: 1rem;
    }
  }
}
